<template>
  <div :class="'alert-' + type">
    <h2>{{ text }}</h2>
    <hr>
    <button class="btn btn-primary"
            @click="$emit('onNext')"
    >
        Continue
    </button>
  </div>
</template>

<script>
  export default {
    props: ['type', 'text']
  }
</script>

<style scoped>
  div {
    padding: 10px;
    text-align: center;
  }

  h2, btn {
    margin: 20px 0;
  }
</style>



